
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

input {
  border: 1px solid darkgrey;
  outline: none;
  border-radius: 4px;
}
.url .header {
  line-height: 64px;
  padding: 0 30px;
  background: royalblue;
  font-size: 24px;
}
.url .url-title {
  font-size: 24px;
  margin-bottom: 30px;
}
.url .env {
  margin: 30px 30px 0;
  border-bottom: 1px solid grey;
}
.url .env .env-item {
    /*margin: 0 0 20px 30px;*/
    font-size: 18px;
    width: 420px;
    display: inline-block;
    margin-bottom: 20px;
}
.url .env .env-item .env-item-title {
      display: inline-block;
      width: 100px;
}
.url .env .env-item .env-item-input {
      font-size: 16px;
      width: 300px;
      line-height: 20px;
}
.url .product {
  margin: 20px;
  position: relative;
  padding-bottom: 30px;
  border-bottom: 1px solid grey;
  overflow: auto;
  zoom: 1;
}
.url .product .product-set {
    float: left;
    width: 400px;
    font-size: 0;
    border: 1px dotted grey;
    padding: 20px 0 0 20px;
}
.url .product .product-set .activity-name {
      margin-bottom: 20px;
}
.url .product .product-set .activity-name .activity-title {
        display: inline-block;
        font-size: 18px;
        width: 100px;
}
.url .product .product-set .activity-name .activity-input {
        width: 280px;
        line-height: 20px;
}
.url .product .product-content {
    float: left;
    margin-left: 30px;
    position: relative;
}
.url .product .product-content .product-content-title {
      margin-bottom: 20px;
      font-size: 18px;
}
.url .product .product-content .product-detail {
      margin-bottom: 16px;
      font-size: 16px;
}
.url .product .product-content .product-detail input {
        line-height: 24px;
        width: 200px;
        margin-right: 20px;
        margin-bottom: 20px;
}
.url .product .product-content .product-content-add {
      position: absolute;
      right: -100px;
      bottom: 74px;
      text-align: center;
      /*margin:0 auto;*/
      font-size: 18px;
      width: 100px;
      line-height: 30px;
      border: 1px dotted grey;
}
.url .product .product-content .product-content-add:hover {
      cursor: pointer;
}
.url .submit {
  margin: 20px auto 0;
  text-align: center;
}
.url .submit .submit-button, .url .submit .submit-clear {
    background: royalblue;
    width: 100px;
    line-height: 30px;
    color: #ffffff;
    font-size: 16px;
}
.url .submit .submit-button:hover, .url .submit .submit-clear:hover {
    cursor: pointer;
}
.url .submit .submit-clear {
    margin: 0;
    background: #ffffff;
    color: royalblue;
}
.url .result {
  padding: 0 30px 30px;
}
